@font-face {
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
body {
  margin: 0;
}

html {
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  outline: 0;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
  border: 0;
}

.container {
  box-sizing: border-box;
  max-width: 1168px;
  padding: 0 10px;
  margin: 0 auto;
}

@media (min-width: 667px) {
  .container {
    padding: 0 20px;
  }
}
@media (min-width: 767px) {
  .container {
    padding: 0 50px;
    min-width: 320px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 30px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.page {
  font-family: "OpenSans", "Arial", sans-serif;
  color: #181a31;
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr min-content;
}

@media (min-width: 1175px) {
  .page {
    grid-template-rows: min-content 1fr min-content;
  }
}
.page.lock {
  transition: 0.3s ease 0s;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.3);
}

@media (max-width: 1175px) {
  .main {
    margin-top: 50px;
  }
}
.menu-button {
  display: none;
}

@media (max-width: 1175px) {
  .menu-button {
    width: 50px;
    height: 50px;
    display: grid;
    place-items: center;
    background: #008ef8;
  }
}
.menu-button__icon {
  width: 22.5px;
  height: 1px;
  display: block;
  background: #ffffff;
  position: relative;
}

.menu-button[aria-expanded=true] .menu-button__svg-icon-t-line {
  transition: transform 0.3s ease 0s;
  transform-origin: center;
  transform: rotate(45deg);
}

.menu-button[aria-expanded=true] .menu-button__svg-icon-b-line {
  transform: scale(0);
}

.menu-button[aria-expanded=true] .menu-button__svg-icon-m-line {
  transition: transform 0.3s ease 0s;
  transform-origin: center;
  transform: translate(4px, 4px) rotate(-45deg);
}

.menu-button__svg-icon-m-line,
.menu-button__svg-icon-t-line,
.menu-button__svg-icon-b-line {
  transition: transform 0.3s ease 0s;
  transform-origin: center;
}

.cart {
  color: #008ef8;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

.cart__icon {
  flex: 0 0 18px;
}

@media (min-width: 667px) {
  .cart__icon {
    margin-right: 8px;
  }
}
.cart__counter {
  z-index: 2;
  display: inline-block;
  position: absolute;
  left: 25px;
  top: 10px;
  user-select: none;
  pointer-events: none;
  padding: 0 4px;
  border-radius: 100px;
  background: #fc4646;
  font-weight: 600;
  font-size: 10px;
  line-height: 130%;
  color: #ffffff;
}

@media (min-width: 667px) {
  .cart__counter {
    left: 9px;
    top: -4px;
  }
}
.cart__text {
  display: none;
}

@media (min-width: 667px) {
  .cart__text {
    display: block;
  }
}
.search {
  width: 100%;
}

.search__form {
  width: 100%;
  position: relative;
}

.search__form-input {
  width: 100%;
  background: rgba(170, 177, 183, 0.1);
  border-radius: 5px;
  border: 0;
  padding: 6px 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #878794;
}

.search__form-input::placeholder {
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}

@media (min-width: 1175px) {
  .search__form-input {
    padding: 5px 10px;
  }
}
.search__form-btn-submit {
  position: absolute;
  z-index: 5;
  background: transparent;
  right: 4px;
  top: 50%;
  transform: translateY(-50%);
}

.search__form-btn-submit svg {
  color: rgba(24, 26, 49, 0.5);
}

@media (min-width: 667px) {
  .search__form-btn-submit {
    right: 8px;
  }
}
@media (min-width: 1175px) {
  .search__form-btn-submit svg {
    color: rgba(255, 255, 255, 0.5);
  }
}
.t-dropdown {
  position: relative;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .t-dropdown:hover::before {
    content: "";
    position: absolute;
    display: block;
    background-color: transparent;
    bottom: -10px;
    left: 0;
    right: 0;
    height: 20px;
    width: 100%;
  }

  .t-dropdown:hover .t-dropdown__trigger::after {
    transform-origin: center;
    transform: rotate(180deg);
  }

  .t-dropdown:hover .t-dropdown__trigger--arrow {
    color: #008ef8;
  }

  .t-dropdown:hover .t-dropdown__trigger--arrow::after {
    background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 7.75L9 12.25L13.5 7.75' stroke='%23008ef8' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  }

  .t-dropdown:hover .t-dropdown__menu {
    display: block;
    position: absolute;
    z-index: 10;
    top: calc(100% + 4px);
  }
}
.t-dropdown.shown .t-dropdown__trigger::after {
  transform-origin: center;
  transform: rotate(180deg);
}

.t-dropdown.shown .t-dropdown__menu {
  display: block;
  position: absolute;
  z-index: 10;
  top: calc(100% + 4px);
}

.t-dropdown.shown .t-dropdown__trigger--arrow::after {
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 7.75L9 12.25L13.5 7.75' stroke='%23008ef8' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}

.t-dropdown__trigger {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}

.t-dropdown__trigger::after {
  transition: transform 0.3s ease 0s;
  margin-left: 5px;
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 3.5px 0 3.5px;
  border-color: #007bff transparent transparent transparent;
}

@media (min-width: 1175px) {
  .t-dropdown__trigger--arrow::after {
    flex: 0 0 18px;
    width: 18px;
    height: 18px;
    border: 0;
    background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 7.75L9 12.25L13.5 7.75' stroke='%23181A31' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  }
}
.t-dropdown__menu {
  min-width: 100%;
  background: #ffffff;
  border-radius: 5px;
  box-shadow: 0 60px 96px rgba(0, 0, 0, 0.01), 0 30.03px 48.05px rgba(0, 0, 0, 0.01), 0 18.09px 29px rgba(0, 0, 0, 0.01), 0 11.6px 18.55px rgba(0, 0, 0, 0.02), 0 7.51px 12px rgba(0, 0, 0, 0.02), 0 4.7px 7.57px rgba(0, 0, 0, 0.02), 0 2.72px 4.35px rgba(0, 0, 0, 0.02), 0 1.2px 1.91px rgba(0, 0, 0, 0.03);
  padding: 8px 0;
  display: none;
}

.t-dropdown__item--active a {
  color: #008ef8;
}

.t-dropdown__item a {
  display: flex;
  padding-right: 16px;
  padding-left: 16px;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .t-dropdown__item a:hover {
    background: #f7f9fe;
    color: #0081e1;
  }
}
.t-dropdown--simple-mobile .t-dropdown__menu {
  padding: 2px 6px 10px;
}

@media (min-width: 667px) {
  .t-dropdown--simple-mobile .t-dropdown__menu {
    padding: 2px 0 10px;
  }
}
@media (max-width: 1175px) {
  .t-dropdown--simple-mobile.shown .t-dropdown__menu {
    display: block;
    position: relative;
    box-shadow: none;
    z-index: 1;
  }

  .t-dropdown--simple-mobile a {
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
  }
}
@media (max-width: 1175px) and (hover: hover) {
  .t-dropdown--simple-mobile a:hover {
    background: #ffffff;
    color: #008ef8;
  }
}
@media (min-width: 1175px) {
  .t-dropdown--simple-mobile .t-dropdown__item a {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }

  .t-dropdown--simple-mobile.shown .t-dropdown__trigger {
    color: #008ef8;
  }
}
.lang-select {
  text-transform: uppercase;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  color: #8b8b97;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .lang-select:hover {
    cursor: pointer;
  }
}
.lang-select__t-dropdown {
  min-width: initial;
  max-width: 51px;
  width: 51px;
}

.lang-select__t-dropdown a {
  padding-top: 5px;
  padding-bottom: 5px;
}

@media (min-width: 1175px) {
  .lang-select__t-dropdown {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }
}
.social-menu {
  display: grid;
  grid-template-columns: repeat(auto-fill, 32px);
}

@media (min-width: 667px) {
  .social-menu {
    display: flex;
    align-items: center;
  }
}
.social-menu__link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 667px) {
  .social-menu__link svg {
    width: 24px;
    height: 24px;
  }
}
.header {
  position: relative;
}

@media (max-width: 1175px) {
  .header {
    position: fixed;
    z-index: 30;
    top: 0;
    width: 100%;
  }
}
@media (min-width: 1175px) {
  .header {
    display: grid;
  }
}
@media (min-width: 667px) {
  .header--show-menu .header__top {
    box-shadow: none;
  }
}
@media (min-width: 1175px) {
  .header__bot-container {
    order: -1;
    background-color: #181a31;
  }
}
.header__top {
  position: relative;
  z-index: 2;
  background: #ffffff;
  box-shadow: 0 60px 96px rgba(0, 0, 0, 0.01), 0 30.03px 48.05px rgba(0, 0, 0, 0.01), 0 18.09px 29px rgba(0, 0, 0, 0.01), 0 11.6px 18.55px rgba(0, 0, 0, 0.02), 0 7.51px 12px rgba(0, 0, 0, 0.02), 0 4.7px 7.57px rgba(0, 0, 0, 0.02), 0 2.72px 4.35px rgba(0, 0, 0, 0.02), 0 1.2px 1.91px rgba(0, 0, 0, 0.03);
}

@media (min-width: 1175px) {
  .header__top {
    background: transparent;
    box-shadow: none;
    z-index: 1;
    width: 100%;
    max-width: 1168px;
    margin: 0 auto;
    position: initial;
  }

  .header__top::after {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    right: 0;
    left: 0;
    height: 60px;
    width: 100%;
    background: #ffffff;
    box-shadow: 0 60px 96px rgba(0, 0, 0, 0.01), 0 30.03px 48.05px rgba(0, 0, 0, 0.01), 0 18.09px 29px rgba(0, 0, 0, 0.01), 0 11.6px 18.55px rgba(0, 0, 0, 0.02), 0 7.51px 12px rgba(0, 0, 0, 0.02), 0 4.7px 7.57px rgba(0, 0, 0, 0.02), 0 2.72px 4.35px rgba(0, 0, 0, 0.02), 0 1.2px 1.91px rgba(0, 0, 0, 0.03);
  }
}
.header__bottom {
  z-index: 1;
  background: #ffffff;
}

@media (min-width: 667px) {
  .header__bottom {
    max-width: 300px;
    right: 0;
  }
}
@media (min-width: 1175px) {
  .header__bottom {
    max-width: 100%;
    background: transparent;
  }
}
.header-top {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header-top__cart {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  width: 50px;
  height: 50px;
  margin-right: 0;
  justify-content: center;
}

@media (min-width: 667px) {
  .header-top__cart {
    width: auto;
    height: auto;
    margin-right: 20px;
    justify-content: flex-start;
  }
}
@media (min-width: 1175px) {
  .header-top__cart {
    margin: 0;
  }
}
.header-bottom {
  --x-pad: 12px;
  padding: 24px var(--x-pad);
  display: grid;
  grid-template-columns: auto auto 1fr;
  grid-template-rows: min-content auto 1fr;
  column-gap: 16px;
  align-items: start;
  height: calc(100vh - 50px);
}

@media (min-width: 667px) {
  .header-bottom {
    grid-template-columns: 1fr;
    --x-pad: 24px;
  }
}
@media (min-width: 1175px) {
  .header-bottom {
    height: initial;
    display: grid;
    align-items: center;
    grid-template-columns: auto 1fr 270px;
    grid-template-rows: auto;
    padding: 0;
    width: 100%;
    max-width: 1168px;
    margin: 0 auto;
  }
}
.header-bottom--closed {
  display: none;
}

.header-bottom--opened {
  position: absolute;
  width: 100%;
}

.header-bottom__title {
  font-weight: 400;
  font-size: 28px;
  line-height: 32px;
}

@media (min-width: 667px) {
  .header-bottom__title {
    margin-bottom: 16px;
  }
}
@media (min-width: 1175px) {
  .header-bottom__title {
    display: none;
  }
}
.header-bottom__lang-select {
  align-self: end;
  line-height: 24px;
}

@media (min-width: 667px) {
  .header-bottom__lang-select {
    margin-left: 100px;
    line-height: 20px;
    align-self: center;
    width: auto;
    text-align: center;
    flex: 1 0 auto;
    justify-content: center;
  }
}
@media (min-width: 1175px) {
  .header-bottom__lang-select {
    margin: 0;
    display: none;
  }
}
.header-bottom__lang-select-desktop {
  display: none;
}

@media (min-width: 1175px) {
  .header-bottom__lang-select-desktop {
    display: block;
    margin-right: 58px;
  }
}
@media (min-width: 667px) {
  .header-bottom__search {
    grid-column: 1/-1;
    padding: 0 var(--x-pad) var(--x-pad);
    width: calc(100% + var(--x-pad) * 2);
    margin: 0 calc(var(--x-pad) * -1);
    border-bottom: 1px solid #d2d9df;
  }
}
@media (min-width: 1175px) {
  .header-bottom__search {
    border: 0;
    padding: 0;
    width: 100%;
    margin: 0;
    grid-column: initial;
    order: 2;
  }
}
.header-bottom__mobile-menu-wrap {
  margin-top: 15px;
  grid-column: 1/-1;
  margin-bottom: 27px;
  max-height: 100%;
  overflow-y: auto;
}

@media (min-width: 1175px) {
  .header-bottom__mobile-menu-wrap {
    margin: 0;
    max-height: initial;
    grid-column: initial;
  }
}
.header-bottom__header-contacts {
  background: #ffffff;
  z-index: 1;
  grid-column: 1/-1;
  border-top: 1px solid #d2d9df;
  padding: 12px var(--x-pad) 0;
  margin: 0 calc(var(--x-pad) * -1);
}

@media (min-width: 667px) {
  .header-bottom__header-contacts {
    padding: 0;
    padding-right: 16px;
    margin: 0;
    margin-right: 16px;
    border: 0;
    border-right: 1px solid #d2d9df;
  }
}
.header-contacts {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto;
  column-gap: 5px;
  row-gap: 9px;
}

@media (min-width: 667px) {
  .header-contacts {
    column-gap: 2px;
    row-gap: 0;
    grid-template-columns: 1fr auto;
    align-items: center;
  }
}
.header-contacts__link-tel {
  font-weight: 600;
  font-size: 22px;
  line-height: 32px;
  color: #008ef8;
}

@media (min-width: 667px) {
  .header-contacts__link-tel {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #181a31;
  }
}
@media (min-width: 1175px) {
  .header-contacts__link-tel {
    justify-self: end;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    grid-column: 1/-1;
  }
}
.header-contacts__social {
  grid-row: 1/-1;
  grid-column: 2/3;
  align-self: start;
  justify-content: end;
  gap: 8px;
}

@media (min-width: 667px) {
  .header-contacts__social {
    align-self: center;
    grid-column: 1/2;
    gap: 12px;
    margin-right: 16px;
  }
}
@media (min-width: 1175px) {
  .header-contacts__social {
    gap: 4px;
    grid-column: 1/2;
    grid-row: 2/3;
    margin-right: 5px;
  }
}
.header-contacts__hitn-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #8b8b97;
}

@media (min-width: 667px) {
  .header-contacts__hitn-text {
    letter-spacing: -1px;
    line-height: 18px;
    text-align: right;
  }
}
@media (min-width: 1175px) {
  .header-contacts__hitn-text {
    letter-spacing: 0.6px;
  }
}
@media (min-width: 1175px) {
  .main-menu--desk-bottom {
    display: flex;
    flex-wrap: wrap;
    margin-right: 129px;
  }

  .main-menu--desk-bottom .main-menu__item-link {
    padding: 20px;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
  }
}
@media (min-width: 1175px) {
  .main-menu--desk-top {
    display: flex;
    flex-wrap: wrap;
  }

  .main-menu--desk-top .main-menu__item-link {
    padding: 10px 20px 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: rgba(255, 255, 255, 0.8);
    position: relative;
  }
}
.main-menu--desk-top .main-menu__item:not(:last-child) {
  margin-right: 11px;
}

.main-menu--desk-top .main-menu__item:not(:last-child) .main-menu__item-link::after {
  content: "";
  display: block;
  right: -5px;
  position: absolute;
  width: 1px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.1);
}

.main-menu__item-link {
  font-weight: 600;
  font-size: 16px;
  padding: 10px 0;
  display: inline-flex;
  line-height: 20px;
  color: #181a31;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .main-menu__item-link:hover {
    color: #008ef8;
  }
}